<template>
	<view class="detail">
		<view class="fixbg" :style="{ 'background-image': 'url('+ songDetail.al.picUrl +')' }"></view>
		<musichead :title="songDetail.name" :icon="true" color="white"></musichead>
		<view class="container" v-show="!isLoading">
			<scroll-view scroll-y="true" >
				 <view class="detail-play" @tap="handleToPlay">
					 <image :src="songDetail.al.picUrl" :class="{ 'detail-play-run': isPlayRotate }" mode=""></image>
					 <text class="iconfont" :class="iconPlay"></text>
					 <view></view>
				 </view>
				 <view class="detail-lyric">
					 <view class="detail-lyric-wrap" :style="{ transform: 'translateY('+ -(lyricIndex - 1) * 82 +'rpx)' }">
					 	<view class="detail-lyric-item" :class="{ active: lyricIndex == index }" v-for="(item,index) in songLyric" :key="index">
							{{item.lyric}}
						</view>
					 </view>
				 </view>
				 <view class="detail-like">
				 	<view class="detail-like-head">喜欢这首歌的人也听</view>
					<view class="detail-like-item" v-for="(item,index) in songSimi" :key="index" @tap="handleToSimi(item.id)">
						<view class="detail-like-img">
							<image :src="item.album.picUrl" mode=""></image>
						</view>
						<view class="detail-like-song">
							<view>{{item.name}}</view>
							<view>
								<image v-if="item.privilege.flag == 4" src="../../static/dujia.png" mode=""></image>
								<image v-if="item.privilege.maxbr == 999000" src="../../static/sq.png" mode=""></image>
								{{item.album.artists[0].name}} - {{item.name}}
							</view>
						</view>
						<text class="iconfont iconbofang"></text>
					</view>
				 </view>
				 <view class="detail-comment">
				 	<view class="detail-comment-head">精彩评论</view>
				 	<view class="detail-comment-item" v-for="(item,index) in songComment" :key="index">
						<view class="detail-comment-img">
							<image :src="item.user.avatarUrl" mode=""></image>
						</view>
						<view class="detail-comment-content">
							<view class="detail-comment-title">
								<view class="detail-comment-name">
									<view>{{item.user.nickname}}</view>
									<view>{{item.time | fromatTime}}</view>
								</view>
								<view class="detail-comment-like">{{item.likedCount | formatCount}}<text class="iconfont iconlike"></text>
								</view>
							</view>
							<view class="detail-comment-text">
								{{item.content}}
							</view>
						</view>
					</view>
				 </view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import musichead from '../../components/musichead/musichead.vue'
	import '@/common/iconfont.css'
	import { songDetail, songSimi, songComment, songLyric, songUrl } from '../../common/api.js'
	export default {
		data() {
			return {
				songDetail: {
					al: {
						picUrl: ''
					}
				},
				songSimi: [],
				songComment: [],
				songLyric: [],
				lyricIndex: 0, // 歌词高亮位置
				iconPlay: 'iconpause', // 控制播放按钮样式
				isPlayRotate: true ,// 控制是否播放中
				isLoading: true // 控制是否显示loading
			}
		},
		components: {
			musichead
		},
		onLoad(options) {
			this.getMusic(options.songId)
		},
		// 退出页面时销毁歌词计时器
		onUnload(){
			this.cancelLyricIndex()
			// #ifdef H5
			this.bgAudioManager.destroy()
			// #endif
		},
		// 其他方式退出时销毁歌词计时器
		onHide(){
			this.cancelLyricIndex()
			// #ifdef H5
			this.bgAudioManager.destroy()
			// #endif
		},
		methods: {
			getMusic(songId){
				// 每次进入歌曲详情页面提交当前歌曲id
				this.$store.commit('NEXI_ID', songId)
				// 数据请求前开始loading
				uni.showLoading({
					title: '加载中...'
				})
				this.isLoading = true;
				// 请求所有数据返回后刷新页面
				Promise.all( [songDetail(songId), songSimi(songId), songComment(songId), songLyric(songId), songUrl(songId) ]).then((res)=>{
					if(res[0][1].data.code == '200'){
						this.songDetail = res[0][1].data.songs[0]
					}
					if(res[1][1].data.code == '200'){
						this.songSimi = res[1][1].data.songs
					}
					if(res[2][1].data.code == '200'){
						this.songComment = res[2][1].data.hotComments
					}
					if(res[3][1].data.code == '200'){
						let lyric = res[3][1].data.lrc.lyric
						let songlyric = lyric.split('\n');
						let result = []
						songlyric.map(item => {
							if(item.length !== 0){ // 歌曲内容为空则不执行
								result.push({
									"time": this.formatTimeToSec( (item.slice(item.indexOf('[')+1,item.indexOf(']'))) ), // 歌曲时间
									"lyric": (item.slice(item.indexOf(']')+1)) // 歌词内容
								})
							}
						})
						this.songLyric = result
					}
					if(res[4][1].data.code == '200'){
						// #ifdef MP-WEIXIN
						this.bgAudioManager = uni.getBackgroundAudioManager()
						this.bgAudioManager.title = this.songDetail.name
						// #endif
						// #ifdef H5
						if(!this.bgAudioManager){
							this.bgAudioManager = uni.createInnerAudioContext()
						}
						this.iconPlay = 'iconbofang1'
						this.isPlayRotate = false
						// #endif
						this.bgAudioManager.src = res[4][1].data.data[0].url || ''
						this.listenLyricIndex()
						// 背景音频播放事件
						this.bgAudioManager.onPlay(()=>{
							this.iconPlay = 'iconpause'
							this.isPlayRotate = true
							this.listenLyricIndex()
						})
						// 背景音频暂停事件
						this.bgAudioManager.onPause(()=>{
							this.iconPlay = 'iconbofang1'
							this.isPlayRotate = false
							this.cancelLyricIndex()
						})
						// 背景音频自然结束播放后跳转下一首歌曲
						this.bgAudioManager.onEnded(()=>{
							this.getMusic( this.$store.state.nextId )
							this.bgAudioManager.autoplay = true // 自动播放
						})
					}
					// 数据请求回后关闭loading
					this.isLoading = false
					uni.hideLoading()
				})
			},
			formatTimeToSec(value){
				let arr = value.split(':')
				return (Number(arr[0]*60) + Number(arr[1])).toFixed(1) // 歌曲时间转换为秒数
			},
			// 控制歌曲播放 play方法触发onPlay pause方法触发onPause
			handleToPlay(){
				if(this.bgAudioManager.paused){
					this.bgAudioManager.play()
				}else{
					this.bgAudioManager.pause()
				}
			},
			listenLyricIndex(){
				clearInterval(this.timer)
				this.timer = setInterval(()=>{
					for(var i=0;i<this.songLyric.length;i++){
						// 当歌曲播放时间大于最后一句歌词播放时间时 歌词高亮显示在最后一行并退出
						if(this.bgAudioManager.currentTime > this.songLyric[this.songLyric.length-1].time){
							this.lyricIndex = this.songLyric.length-1
							break
						}
						// 当歌曲正在播放时间大于本条歌词对应时间并小于下条歌词时间时 本句歌词高亮
						if(this.bgAudioManager.currentTime > this.songLyric[i].time && this.bgAudioManager.currentTime < this.songLyric[i+1].time){
							this.lyricIndex = i
							break
						}
					}
					
				},500)
			},
			cancelLyricIndex(){
				clearInterval(this.timer)
			},
			handleToSimi(songId){
				this.getMusic(songId)
			}
		}
	}
</script>

<style scoped> 
.detail{
	
}
.detail-play{
	width: 580rpx;
	height: 580rpx;
	/* 背景图片 绝对路径 */
	background: url(~@/static/disc.png);
	background-size: cover;
	margin: 214rpx auto 0 auto;
	position: relative;
}
.detail-play image{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	width: 370rpx;
	height: 370rpx;
	border-radius: 50%;
	animation: 10s linear move infinite;
	animation-play-state: paused;
}
.detail-play .detail-play-run{
	animation-play-state: running;
}
/* 旋转动画 */
@keyframes move{
	form {
		transform: rotate(0deg)
	}
	to {
		transform: rotate(360deg)
	}
}
.detail-play text{
	width: 100rpx;
	height: 100rpx;
	font-size:100rpx;
	color: white;
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	
}
.detail-play view{
	width: 230rpx;
	height: 360rpx;
	background: url(~@/static/needle.png);
	position: absolute;
	left: 100rpx;
	right: 0;
	top: -200rpx;
	margin: auto;
	background-size: cover;
}

.detail-lyric{
	font-size:32rpx;
	line-height: 82rpx;
	height: 246rpx;
	text-align: center;
	overflow: hidden;
	color: #6f6e73;
}
.detail-lyric-wrap{
	transition: .5s;
}
.detail-lyric-item{
	height: 82rpx;
}
.detail-lyric-item.active{
	color: #00cbff;
}

.detail-like{
	margin: 0 30rpx;
}
.detail-like-head{
	font-size: 36rpx;
	color: white;
	margin: 50rpx 0;
}
.detail-like-item{
	display: flex;
	align-items: center;
	margin-bottom: 28rpx;
}
.detail-like-item text{
	font-size:50rpx;
	color: #c6c2bf;
}
.detail-like-img{
	width: 82rpx;
	height: 82rpx;
	border-radius: 20rpx;
	overflow: hidden;
	margin-right: 20rpx;
}
.detail-like-img image{
	width: 100%;
	height:100%;
}
.detail-like-song{
	flex: 1;
	color: #c6c2bf;
}
.detail-like-song view:nth-child(1){
	font-size: 28rpx;
	color: white;
	margin-bottom: 12rpx;
}
.detail-like-song view:nth-child(2){
	font-size: 22rpx;
}
.detail-like-song image{
	width: 26rpx;
	height: 20rpx;
	margin-right: 10rpx;
}

.detail-comment{
	margin: 0 30rpx;
}
.detail-comment-head{
	font-size: 36rpx;
	color: white;
	margin: 50rpx 0;
}
.detail-comment-item{
	margin-bottom: 28rpx;
	display: flex;
}
.detail-comment-img{
	width: 64rpx;
	height: 64rpx;
	border-radius: 50%;
	overflow: hidden;
	margin-right: 18rpx;
}
.detail-comment-img image{
	width: 100%;
	height: 100%;
}
.detail-comment-content{
	flex: 1;
	color: #cbcacf;
}
.detail-comment-title{
	display: flex;
	justify-content: space-between;
}
.detail-comment-name{

}
.detail-comment-name view:nth-child(1){
	font-size:26rpx;
}
.detail-comment-name view:nth-child(2){
	font-size:20rpx;
}
.detail-comment-like{
	font-size:28rpx;
}
.detail-comment-text{
	font-size:28rpx;
	line-height: 40rpx;
	color: white;
	margin-top:20rpx;
	border-bottom: 1px solid #e0e0e0;
	padding-bottom: 40rpx;
}
</style>
